import request from '../request'
//
export function gunInOutList(data) {
  return request({
    url: '/eoms/gunHandovers/page',
    method: 'post',
    data

  })
}

//
export function carInOutList(data) {
  return request({
    url: '/eoms/carHandovers/page',
    method: 'post',
    data

  })
}

//
export function gunInOutCount(data) {
  return request({
    url: '/eoms/gunHandovers/count',
    method: 'post',
    data

  })
}

//
export function carInOutCount(data) {
  return request({
    url: '/eoms/carHandovers/count',
    method: 'post',
    data

  })
}

//
export function pacCountByBank(data) {
  return request({
    url: '/eoms/package/countByBank',
    method: 'post',
    data,
    headers: {
      'content-type': 'application/json'
    }


  })
}




