<template>
  <div id="commonProblem">
    <el-card class="box-card cPro">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <div class="text item l-question">
        <!--  -->
        <el-table class="my-table" :data="tableData" v-loading="listLoading" style="width: 100%">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="" v-if="props.row.finish">
                  <span>{{ props.row.answer }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column label="" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
          <el-table-column label="" prop="question"></el-table-column>
          <el-table-column label="" width="180" align="center" v-if="power==1">
            <template slot-scope="props">
              <span :class="props.row.finish==0?'nore':'fied'">{{ props.row.finish==0?'':'' }}</span>
              <el-button v-if="power==1" size="mini" round type="primary" class="answerBtn" @click="anQ(props.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <pagination v-show="pages.total>0" :total="pages.total" :page.sync="pages.page" :limit.sync="pages.size" @pagination="getQ" layout="prev, pager, next, sizes, total, jumper" />
        <!--  -->
        <el-button type="primary" class="l-ask" @click="openDia" v-if="power==1"></el-button>
      </div>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <div class="text item"></div>
      <div class="text item"> B  7 </div>
      <div class="text item"></div>
      <div class="text item">john@beidoucom.com</div>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <div class="text item"></div>
      <div class="text item"> B  7 </div>
      <div class="text item"></div>
      <div class="text item">jeff.lv@beidoucom.com</div>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>

      <a class="text item" href="download/BDA09.pdf" download="BDA09"></a>

      <a class="text item" href="download/beidou install setup.exe" download="beidou install setupX64.exe"></a>
      <a class="text item" href="download/.xls" download=""></a>
      <a class="text item" href="download/.xls" download=""></a>
      <a class="text item" href="download/.xls" download=""></a>
      <a class="text item" href="download/.xls" download=""></a>
      <a class="text item" href="download/.xls" download=""></a>
      <a class="text item" href="download/.xls" download=""></a>
      <a class="text item" href="download/.xls" download=""></a>
      <a class="text item" href="download/.xls" download=""></a>
      <a class="text item" href="download/.xls" download=""></a>

    </el-card>
    <!--  -->
    <el-dialog id="myDialog" title="" :visible.sync="qusDia" center>
      <el-form ref="dataForm" :rules="rules" :model="Cqus" label-position="left">
        <el-form-item label="" prop="cont" class="l-textarea">
          <el-input type="textarea" :rows="14" placeholder="" v-model="Cqus.cont" maxlength="200" show-word-limit> </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="createQ"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog id="myDialog1" title="" :visible.sync="anDia" center>
      <el-form ref="anForm" :rules="anRules" :model="Anqus" label-position="left">
        <el-form-item label="" prop="ques" class="l-textarea">{{Anqus.ques}}</el-form-item>
        <el-form-item label="" prop="cont" class="l-textarea">
          <el-input type="textarea" :rows="14" placeholder="" v-model="Anqus.cont" maxlength="400" show-word-limit> </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="answerQ(Anqus.id)"></el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import Pagination from "../../components/Pagination";
import { queList, queAdd, answer } from "../../api/managementArchives/gunFiles";
export default {
  name: "commonProblem",

  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 
      qusDia: false,// 
      Cqus: {// 
        cont: ''
      },
      rules: {// 
        cont: [{ required: true, message: "" }]
      },
      listLoading: false,// loading
      tableData: [],
      pages: {
        page: 1,
        size: 10,
        total: 0
      },
      indexMethod: 1, // 
      anDia: false,// 
      Anqus: {// 
        id: undefined,
        ques: 'xx',
        cont: ''
      },
      anRules: {// 
        cont: [{ required: true, message: "" }]
      },
    }
  },
  mounted() {
    // 
    this.getQ();
  },
  methods: {
    getQ() {// 
      let _t = this, obj = { size: _t.pages.size, page: _t.pages.page };
      _t.listLoading = true;
      _t.tableData = [];
      if (_t.power != 1) {
        obj.finish = 1;
      };
      queList(obj).then(res => {
        // console.log(res)
        if (res.success) {
          _t.tableData = res.data.records;// 
          _t.pages.total = res.data.total;// 
          _t.indexMethod = (_t.pages.page - 1) * _t.pages.size + 1;
        };
      }).finally(() => {
        _t.listLoading = false;
      });
    },
    searchQ() {// 
    },
    createQ() {// 
      let _t = this;
      _t.$refs['dataForm'].validate((valid) => {
        if (valid) {
          queAdd({ question: _t.Cqus.cont }).then(res => {
            if (res.success) {
              _t.$message.success('!');
              _t.qusDia = false;// 
              _t.pages.page = 1;// 
              _t.getQ();// 
            } else {
              _t.$message.error('');
            };
          }).catch(err => {
            _t.$message.error('')
          });
        }
      });
    },
    openDia() {// 
      let _t = this;
      _t.qusDia = true;// 
      _t.$refs.dataForm && _t.$refs.dataForm.resetFields();// 
    },
    anQ(row) {// 
      let _t = this;
      _t.anDia = true;// 
      _t.$refs.anForm && _t.$refs.anForm.resetFields();// 
      // 
      _t.Anqus.id = row.id;
      _t.Anqus.ques = row.question;
      _t.Anqus.cont = row.answer;
    },
    answerQ(idpost) {// 
      let _t = this;
      _t.$refs['anForm'].validate((valid) => {
        if (valid) {
          answer({ id: idpost, answer: _t.Anqus.cont }).then(res => {
            if (res.success) {
              _t.$message.success('');
              _t.anDia = false;// 
              _t.getQ();// 
            } else {
              _t.$message.error('');
            };
          }).catch(err => {
            _t.$message.error('')
          });
        }
      });
    }
  }
}
</script>
<style lang="scss" scoped>
.text {
  font-size: 14px;
}
.item {
  margin-bottom: 16px;
  margin-top: 16px;
}
a.item {
  color: #4653c9;
  padding: 0 10px 10px 0px;
  min-width: 192px;
  display: inline-block;
  margin: 0;
  &:hover {
    text-decoration: underline;
  }
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  flex: 1;
  margin: 20px;
}
</style>
<style lang="scss">
$headerBarH: 120px;
#commonProblem {
  height: calc(100vh - #{$headerBarH}) !important;
  // padding: 20px;
  display: flex;
  flex-wrap: wrap;
  .cPro {
    // 
    flex: 1 1 100%;
    .el-card__body {
      // body
      // min-height: 380px;
      overflow: hidden;
    }
    // 
    .l-question {
      height: 100%;
      display: flex;
      flex-direction: column;
      .el-form-item {
        // 
        margin-bottom: 0;
      }
      .l-content {
        // 
        flex: 1 1 100%;
        overflow-y: auto;
        background-color: #f9f7f7;
        list-style: none;
        padding: 10px;
        margin: 10px 0;
        li {
          // 
          margin-bottom: 20px;
          h5 {
            margin: 5px 0;
            font-size: 14px;
          }
          .cont {
            font-size: 13px;
          }
        }
      }
      .l-ask {
        // 
        margin-right: auto;
      }
    }
  }
  // 
  .el-dialog {
    .l-textarea {
      .el-form-item__content {
        height: auto;
        textarea {
          font: 400 13.3333px Arial;
          resize: none;
        }
      }
    }
  }
  // 
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 80%;
  }
  .expanded + tr td,
  .expanded + tr {
    background-color: #f3f3f3;
  }
  // 
  .my-table {
    height: 208px;
    span.nore {
      // 
      color: #f00;
    }
    span.fied {
      // 
      color: #05c76d;
    }
    .answerBtn {
      // 
      margin-left: 8px;
    }
  }
}
</style>