<template>
  <div>
    <el-dialog title="" :visible.sync="openUploadDialog" v-loading="loading" width="40%">
      <div class="myBox">
          <div class="el-upload__tip red topTip" slot="tip">Excel <span class="importDate">{{date}}</span> </div>
        <el-upload drag ref="upload" :limit="limitNum" :auto-upload="false" accept=".xls" :on-remove="handleRemove" :action="UploadUrl()" :before-upload="beforeUploadFile" :on-change="fileChange" :on-exceed="exceedFile" :on-success="handleSuccess" :on-error="handleError" :file-list="fileList">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text"><em></em></div>
          <div class="el-upload__tip" slot="tip">xls,10M</div>
          <div class="el-upload__tip red" slot="tip"></div>
        </el-upload>
      </div>

      <br />
      <div slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" @click="uploadFile"></el-button>
        <el-button size="small" @click="openUploadDialog=false"></el-button>

      </div>

    </el-dialog>
  </div>
</template>
<script>
import {importFileReq} from "../../api/managementArchives/common";
import { formatDate } from '../../common'

export default {
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    limitNum: {
      type: Number,
      default: 1
    },
    uploadData: {
      type: Object,
      required: true,
    },
    dataUpdated: {
      type: Boolean,
      default: false
    },
     date: {
      type: String,
      default: false
    }

  },
  data() {
    return {
      fileList: [],
      loading: false

    }
  },
  computed: {
    openUploadDialog: {
      get() {
        return this.visible
      },
      set(val) {
        this.$emit('update:visible', val) // openUploadDialog
        this.$refs.upload.clearFiles();
        this.fileList = []
      }
    }
  },

  methods: {
    formatDate,
    handleRemove(file, fileList) {
      if (file && file.status === "success") {
        //
        this.fileList = []
      }
    },
    // 
    exceedFile(files, fileList) {
      this.$message.warning(` ${this.limitNum}  ${files.length + fileList.length} `);
    },
    // 
    fileChange(file, fileList) {
      this.fileList = [file.raw]
    },
    // , , false  Promise  reject
    beforeUploadFile(file) {

      let extension = file.name.substring(file.name.lastIndexOf('.') + 1);
      let size = file.size / 1024 / 1024;
      if (extension !== 'xls' && extension !== 'XLS') {
        this.$message.warning('.xls');
        return false
      }
      if (size > 10) {
        this.$message.warning('10M');
        return false
      }
    },
    // 
    handleSuccess(res, file, fileList) {
      this.$message.success('');
    },
    // 
    handleError(err, file, fileList) {
      this.$message.error('');
    },
    UploadUrl: function () {
      // actionurlapi404action
      return ""
    },
    uploadFile() {
      this.$emit('dataUpdated', false);
      if (this.fileList.length === 0) {
        this.$message.warning('');
      } else {
            this.loading = true;
            importFileReq({ file: this.fileList[0], creator: JSON.parse(sessionStorage.userInfo).UserName, type: this.uploadData.type, date: this.date })
              .then(res1 => {
                this.$message.success(res1.data);
                this.$emit('dataUpdated', true);
                this.openUploadDialog = false;
              })
              .catch(() => {

              }).finally(() => {
                this.$refs.upload.clearFiles();
                this.loading = false;

                this.fileList = []
              });
      }
    }

  }
}
</script>
<style lang="scss">
.myBox {
  //
  .el-form-item__label {
    height: 60px;
    line-height: 60px;
  }
}
</style>
<style scoped lang="scss">
$theme-color: #324890;
.myBox {
//   display: flex;
  //
  .el-upload__tip {
    font-size: 14px;
    &.red {
      color: red;
      font-size: 16px;
    }
    &.topTip{
    margin-bottom:20px;
    }
  }
  .importDate{
      color:$theme-color;
  }
}
</style>