
import request from '../../request'

//
export function query(data) {
    return request({
        url: '/eoms/bankWorkers/query',
        method: 'post',
        data,
        headers:{'content-type':'application/json'}
    })
}
// 
export function queryPage(data) {
    return request({
        url: '/eoms/bankWorkers/page',
        method: 'post',
        data,
        headers:{'content-type':'application/json'}
    })
}
//
export function saveOrUpdate(data) {
    return request({
        url: '/eoms/bankWorkers/saveOrUpdate',
        method: 'post',
        data,
        headers:{'content-type':'application/json'}
    })
}

