<template>
  <div id="bankPackageInOut" class="app-container" :class="{adminClass:(userInfo.Power==4)}">

    <div class="filter-container">
      <div class="title" v-show="userInfo.Power==4"></div>
      <div>
        <!--  -->
        <div class="selectBox">
          <el-form :inline="true" class="filterTool" :rules="mdRule" ref="mdForm" :model="mdForm" label-width="80px">
            <el-form-item prop="searchDate">
              <el-date-picker v-model="mdForm.searchDate" type="date" placeholder="" value-format="timestamp" :clearable="false" :picker-options="pickerOptions">
              </el-date-picker>
            </el-form-item>
            <el-button class="filter-item" type="primary" @click="getList" icon="el-icon-search"></el-button>
            <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu" :loading="exportLoading"></el-button>
            <el-button class="filter-item" type="primary" @click="$router.push('/packageInOut')" v-if="userInfo.Power!=5"></el-button>
          </el-form>
        </div>
      </div>

    </div>
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" show-summary :cell-class-name="getCellClass"  :summary-method="getSummaries" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName" :header-cell-style="{textAlign: 'center'}" :cell-style="{ textAlign: 'center' }">
        <el-table-column label="" width="150" prop="pointName">
        </el-table-column>
        <el-table-column label="" min-width="150">
          <el-table-column label="" min-width="30" prop="morningTailBoxNum">
          </el-table-column>
          <el-table-column label="" min-width="30" prop="morningNullNameBoxNum">
          </el-table-column>
          <el-table-column label="" min-width="30" prop="morningNullNamePackageNum">
          </el-table-column>
          <el-table-column label="" min-width="30" prop="morningTemplateBoxNum">
          </el-table-column>
        </el-table-column>
        <el-table-column label="" min-width="150">
          <el-table-column label="" min-width="30" prop="nightTailBoxNum">
          </el-table-column>
          <el-table-column label="" min-width="30" prop="nightNullNameBoxNum">
          </el-table-column>
          <el-table-column label="" min-width="30" prop="nightNullNamePackageNum">
          </el-table-column>
          <el-table-column label="" min-width="30" prop="nightTemplateBoxNum">
          </el-table-column>
        </el-table-column>
        <el-table-column label="()" min-width="150">
          <el-table-column label="" min-width="30" prop="warehouseTailBoxNum">
          </el-table-column>
          <el-table-column label="" min-width="30" prop="warehouseNullNameBoxNum">
          </el-table-column>
          <el-table-column label="" min-width="30" prop="warehouseNullNamePackageNum">
          </el-table-column>
          <el-table-column label="" min-width="30" prop="warehouseTemplateBoxNum">
          </el-table-column>
        </el-table-column>
       

        <el-table-column label="" align="center" width="150" class-name="small-padding fixed-width" fixed="right" v-if="userInfo.Power!=5">
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="handleModify(row)"></el-button>
          </template>
        </el-table-column>
        <el-table-column label="" width="150" fixed="right" v-if="userInfo.Power!=5">
          <template slot-scope="{row}">
            <!-- <span class="link-type" title="" @click="showModifyRecord(row)">{{ row.beManualModify==1?row.updateTime:'' }}</span> -->
            <el-button type="primary" size="mini" @click="showModifyRecord(row)" :disabled="row.beManualModify!=1"></el-button>
           

          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog id="myDialog" title="" :visible.sync="dialogFormVisible" center>
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left" class="formStyle">
        <el-form-item label="" prop="warehouseTailBoxNum">
          <el-input v-model="temp.warehouseTailBoxNum" />
        </el-form-item>
        <el-form-item label="" prop="warehouseNullNameBoxNum">
          <el-input v-model="temp.warehouseNullNameBoxNum" />
        </el-form-item>
        <el-form-item label="" prop="warehouseNullNamePackageNum">
          <el-input v-model="temp.warehouseNullNamePackageNum" />
        </el-form-item>
        <el-form-item label="" prop="warehouseTemplateBoxNum">
          <el-input v-model="temp.warehouseTemplateBoxNum" />
        </el-form-item>
      </el-form>
      <div class="dialogBottomHint"></div>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm('dataForm')"></el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogTableVisible">
      <el-table :data="recordData">
        <el-table-column property="operator" label="" min-width="80"></el-table-column>
        <el-table-column property="operateTime" label="" min-width="120"></el-table-column>
        <el-table-column property="warehouseTailBoxNumChange" label="" min-width="80"></el-table-column>
        <el-table-column property="warehouseNullNameBoxNumChange" label="" min-width="80"></el-table-column>
        <el-table-column property="warehouseNullNamePackageNumChange" label="" min-width="80"></el-table-column>
        <el-table-column property="warehouseTemplateBoxNumChange" label="" min-width="80"></el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import { queryPackageWarehouse, modifyPackageWarehouse, queryByWarehouseId } from "../../api/inOut";//
import { selectBank } from "../../api/managementArchives/bank";
import { exportExcelReq } from "../../api/managementArchives/common";
import { query } from "../../api/managementArchives/bankPersonManagement";
let myLoading = undefined; // 


export default {
  name: "bankPackageInOut",
  components: { Pagination },
  data() {
    var checkNumStr = (rule, value, callback) => {
      let re = /^[0-9]+$/
      if (value == undefined) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (!re.test(value)) {
          callback(new Error(''))
        } else {
          callback()
        }
      }, 100)
    };
    //
    let timeRule = (rule, value, callback) => {
      if (!value) {
        callback(new Error(""));
      } else {
        callback();
      }

    };
    return {
      userInfo: JSON.parse(sessionStorage.getItem("userInfo")),
      tableKey: 0,
      list: [],
      total: 0,
      indexMethod: 1,
      listLoading: true,
      listQuery: {
        page: 1,
        limit: 10,
        keyword: undefined
      },
      mdForm: {
        searchDate: null,
        bankIds: []
      },
      mdRule: {
        searchDate: [{ trigger: "change", message: "", validator: timeRule, }],
      },
      bankObj: {},
      exportLoading: false,
      dialogFormVisible: false,
      temp: {},
      rules: {
        warehouseTailBoxNum: [
          { trigger: "blur", required: true, validator: checkNumStr }
        ],
        warehouseNullNameBoxNum: [
          { trigger: "blur", required: true, validator: checkNumStr }
        ],
        warehouseNullNamePackageNum: [
          { trigger: "blur", required: true, validator: checkNumStr }
        ],
        warehouseTemplateBoxNum: [
          { trigger: "blur", required: true, validator: checkNumStr }
        ],

      },
      dialogTableVisible: false,
      recordData: [],
      pickerOptions: {
        disabledDate: (time) => {
          //
          return time.getTime() > Date.now()
        },
      },

    };
  },
  created() {
    if (this.userInfo.Power != 5) {
      this.mdForm.searchDate = Number(this.$route.query.time);
      this.mdForm.bankIds = this.$route.query.bankIds
      this.getList(); //
    } else {
      this.mdForm.searchDate = new Date().getTime();
      this.getPersonDetail(this.userInfo.PersonId).then((data) => {
        this.mdForm.bankIds = data[0].parentBankId;
        this.getList()
      })
    }


  },
  methods: {
      getCellClass({ row, column, columnIndex }) {
      if (row[column.property]<0) {
        return 'red'
      } else {
        return ''
      }
    },
      getSummaries(param) {
        const { columns, data } = param;
        const sums = [];
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '';
            return;
          }
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            // sums[index] += ' ';
          } else {
            sums[index] = '--';
          }
        });

        return sums;
      },
    showModifyRecord(row) {
      this.recordData = [];
      queryByWarehouseId(
        { warehouseId: row.id }
      )
        .then(res => {
          if (res.status == 0) {
            this.dialogTableVisible = true;
            this.recordData = res.data;
          }
        })

    },
    handleModify(row) {
      this.temp = {
        warehouseTailBoxNum: row.warehouseTailBoxNum,
        warehouseNullNameBoxNum: row.warehouseNullNameBoxNum,
        warehouseNullNamePackageNum: row.warehouseNullNamePackageNum,
        warehouseTemplateBoxNum: row.warehouseTemplateBoxNum,
        pointId: row.pointId
      }
      if (row.id) {
        this.temp.id = row.id;
      }
      this.dialogFormVisible = true;
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          myLoading = this.$loading({ target: "#mydialog .el-dialog" });


          let time = new Date(this.mdForm.searchDate)
          let date = time.getFullYear() + '-' + (this.addZero(time.getMonth() + 1)) + '-' + this.addZero(time.getDate())
          this.temp.actuallyTime = date + ' 00:00:00';
          modifyPackageWarehouse(
            this.temp
          )
            .then(res => {
              this.$message.success('')
              this.getList();
            })
            .finally(() => {
              this.dialogFormVisible = false;
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    getPersonDetail(personId) {
      this.personDetail = {}
      return new Promise((resolve, reject) => {
        query({ id: personId })
          .then(res => {
            if (res.status == 0) {
              resolve(res.data)

            }
          }).catch(error => {
          })
          .finally(() => {
          });
      });

    },
    getSelect() {
      return new Promise((resolve, reject) => {
        selectBank().then(res => {
          resolve(res)

        });
      })

    },
    addZero(num) {
      return num < 10 ? "0" + num : num;
    },
    getList() {
      let that = this;
      this.listLoading = true;
      that.list = [];
      let time = new Date(this.mdForm.searchDate)
      let date = time.getFullYear() + '-' + (this.addZero(time.getMonth() + 1)) + '-' + this.addZero(time.getDate())
      queryPackageWarehouse(
        { actuallyTime: date + ' 00:00:00', bankIds: this.mdForm.bankIds }
      )
        .then(res => {
          if (res.status == 0) {
            that.listLoading = false;
            for (let i in res.data.pointPackage) {
              that.list.push(res.data.pointPackage[i]);
              //id
              that.list.sort((a,b)=>{
              return a.pointId-b.pointId
              })
            }
          } else {
          }
        })
        .finally(() => {
          that.listLoading = false;
        });
    },
    //
    handleFilter() {
      this.$router.push({ name: 'gunInoutChild', params: { keyword: this.listQuery.keyword } });

    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      // 
      _t.$refs["mdForm"].validate(valid => {
        if (valid) {
          this.exportLoading = true;
          exportExcelReq({ excelType: 39, title: '', times: [ new Date(this.mdForm.searchDate).setHours(0,0,0,0)], bankIds: [this.mdForm.bankIds] }).then(res => {
            try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
              let fileName = "" + ".xls";
              let blob = new Blob([res], { type: "application/excel" }); // pdf
              let link = document.createElement("a");
              link.href = window.URL.createObjectURL(blob);
              link.download = fileName;
              link.click();
              link.remove();
            }
          }).finally(() => {
            this.exportLoading = false;
          });
        }
      })
    }

  }
};
</script>
<style lang="scss">
#bankPackageInOut {
    .red{
    color:red;
  }
  // 
  $border-color: rgb(190, 190, 190);
  thead.is-group.has-gutter {
    tr:first-child {
      // 
      th {
        border-right-color: $border-color;
      }
    }
    tr:nth-child(2) {
      // 
      th:nth-child(4n) {
        border-right-color: $border-color;
      }
    }
  }
  .el-table__body-wrapper,
  .el-table__footer-wrapper {
    td:first-child,
    td:nth-child(4n + 1) {
      // 
      border-right-color: $border-color;
    }
    td:nth-last-of-type(2) {
      // 
      border-right-color: $border-color;
    }
  }
  //
  .el-table__footer {
    td .cell {
      text-align: center;
    }
  }
  //
  .dialogBottomHint {
    color: red;
    padding-top: 20px;
    font-size: 20px;
  }

  //
  .selectBox {
    display: flex;
    align-items: center;

    .el-select {
      width: 300px;
    }
    .el-input__inner,
    .el-form-item__content,
    .el-cascader,
    .el-input {
      width: 300px;
      height: 30px !important;
      line-height: 30px;
    }
    .el-cascader__search-input {
      background-color: #f9f9f9;
    }
    .el-form-item {
      margin-bottom: 0;
      width: 300px;
    }
  }
}
</style>
<style scoped lang="scss">
//
.filter-container {
  position: relative;
  .title {
    position: absolute;
    margin-left: 20px;
    left: 0;
    font-size: 20px;
  }
}
</style>










