<template>
  <router-view v-if="$route.path!=='/schedualManagement/schedualChild'">

  </router-view>
  <div class="app-container" id="schedualChild" v-else>

    <div class="filter-container">
      <el-date-picker class="filter-item dateRange" v-model="listQuery.selectDate" @change="changegToGetList" type="week" format="yyyy  WW " placeholder="" :clearable="false" :picker-options="{'firstDayOfWeek': 1}">
