      <!-- <el-button class="filter-item" type="primary" @click="handleCreate" :disabled="(!showGenerate&&!showPublish)||dateDisabled" icon="el-icon-circle-plus-outline"></el-button> -->

      <el-button class="filter-item" type="primary" @click="handleGenerate" :disabled="(!showGenerate&&!showPublish)||dateDisabled||hasNormalSchedule" icon="iconfont iconzidong"></el-button>
      <el-button class="filter-item" type="primary" @click="handleScehduling" :disabled="(!showGenerate&&!showPublish)||dateDisabled" icon="iconfont iconshoudong1"></el-button>
      <el-button class="filter-item" type="primary" @click="open" :disabled="!showPublish||dateDisabled" icon="iconfont iconfabu1"></el-button>
      <el-button class="filter-item filter-item-btn" type="primary" :disabled="showGenerate" @click="exportExcel" icon="iconfont icondaochu"></el-button>
    </div>
    <div class="tableBox">
      <el-form :model="tableForm" ref="tableForm">
        <el-table :key="tableKey" height="100%" v-loading="listLoading" ref="tableRef" :data="tableForm.list" fit highlight-current-row :row-class-name="tableRowClassName" :rules="listRules" @filter-change="handleFilterChange">
          <el-table-column label="" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
          <el-table-column label="" min-width="100px" align="center" prop="listProp">
            <template slot-scope="{row}">
              <span>{{ row.routeName }}</span>
            </template>
          </el-table-column>

          <el-table-column label="" min-width="60px" align="center">
            <template slot-scope="scope">
              <span>{{ formateStatus(scope.row.status) }}</span>

            </template>
          </el-table-column>

          <!-- <el-table-column label="" min-width="130px" align="center">
            <template slot-scope="scope">
              <el-select filterable v-model="scope.row.carId" class="filter-item" :class="{removeClass:judgeRemoveIn(carOptions,scope.row.carId)}" :disabled="!showPublish||scope.row.statusDisabled" placeholder="" @change="selectChange(scope.row,'car')">
                <el-option v-for="item in carOptions" :key="item.id" :label="item.licenseNo" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
              </el-select>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="110px" align="center">
            <template slot-scope="scope">
              <el-select filterable  v-model="scope.row.leaderId" :disabled="!showPublish||scope.row.statusDisabled" class="filter-item" :class="{removeClass:judgeRemoveIn(teamLeaderOptions,scope.row.leaderId)}" placeholder="" @change="selectChange(scope.row,'group')">
                <el-option v-for="item in teamLeaderOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
              </el-select>
            </template>
          </el-table-column> -->
          <el-table-column label="" min-width="80px" align="center">
            <template slot-scope="{row}">
              <span :class="{red:row[row.id+'car'+row.carId]==true,removeClass:judgeRemoveIn(carOptions,row.carId)}">{{ row.carLicenseNo }}</span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="60px" align="center">
            <template slot-scope="{row}">
              <span :class="{red:row[row.id+'-'+row.leaderId]==true}">{{ row.leaderName }}</span>
            </template>
          </el-table-column>

          <el-table-column label="" min-width="60px" align="center" prop="driverName">
            <template slot-scope="scope">
              <span :class="{red:scope.row[scope.row.id+'-'+scope.row.driverId]==true}">{{ scope.row.driverName }}</span>
            </template>
          </el-table-column>
          <el-table-column label="1" min-width="60px" align="center" prop="safetyName">
            <template slot-scope="scope">
              <span :class="{red:scope.row[scope.row.id+'-'+scope.row.safetyId]==true}">{{ scope.row.safetyName }}</span>
            </template>
          </el-table-column>

          <el-table-column label="2" min-width="60px" align="center" prop="additionSafetyName">
            <template slot-scope="scope">
              <span :class="{red:scope.row[scope.row.id+'-'+scope.row.additionSafetyId]==true}">{{ scope.row.additionSafetyName }}</span>
            </template>
          </el-table-column>
          <el-table-column label="1" min-width="60px" align="center" prop="businessName">
            <template slot-scope="scope">
              <span :class="{red:scope.row[scope.row.id+'-'+scope.row.businessId]==true}">{{ scope.row.businessName }}</span>
            </template>
          </el-table-column>
          <el-table-column label="2" min-width="60px" align="center" prop="addBusinessName">
            <template slot-scope="scope">
              <span :class="{red:scope.row[scope.row.id+'-'+scope.row.addBusinessId]==true}">{{ scope.row.addBusinessName }}</span>
            </template>
          </el-table-column>
          <el-table-column label="" width="80px" align="center" prop="addBusinessName">
            <template slot-scope="scope">
              <span>{{ scope.row.publish==1?'':'' }}</span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="80px" align="center" prop="type" column-key="type" :filters='typeArr' :filter-method="filterTypeTag" :filtered-value="filteredTypeValue">
            <template slot-scope="scope">
              <span>{{ formateType(scope.row.type) }}</span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="80px" fixed="right" align="center" prop="sortDate" column-key="sortDate" :filters='dateArr' :filter-method="filterTag" :filtered-value="filteredValue">
            <template slot-scope="scope">
              <span>{{ scope.row.planYear+'-'+scope.row.planMonth+'-'+scope.row.planDay }}</span>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column label="" align="center" fixed="right" min-width="200" class-name="small-padding fixed-width">
            <template slot-scope="{row}">
              <el-button type="primary" size="mini" :disabled="row.publish==1||row.statusDisabled||dateDisabled" @click="handleEdit(row)" icon="el-icon-edit-outline"></el-button>

              <!-- <el-button type="primary" size="mini" @click="gotask(row)" icon="iconfont icontask"></el-button>
              <el-button type="danger" size="mini" :disabled="row.status==2||row.status==4||row.publish==1||dateDisabled" @click="saveSubmit(row,'delete')" icon="iconfont iconbaofei"></el-button> -->

            </template>
          </el-table-column>
        </el-table>
      </el-form>
      <el-dialog title="" id="mydialog" :visible.sync="addVisible" append-to-body center>
        <el-form ref="addForm" :model="addTemp" label-position="left" :rules="addRules" class="formStyle">
          <el-form-item label="" prop="addDate">
            <el-date-picker class="filter-item" v-model="addTemp.addDate" align="right" type="date" value-format="yyyy_M_d" placeholder="" :picker-options="pickerOptions">
            </el-date-picker>
