            </el-select>
          </el-form-item>
          <el-form-item label="" prop="carId">
            <el-select filterable v-model.number="addTemp.carId" class="filter-item" placeholder="Please select">
              <el-option v-for="item in carOptions" :key="item.id" :label="item.licenseNo" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="leaderId">
            <el-select filterable v-model.number="addTemp.leaderId" class="filter-item" placeholder="Please select">
              <el-option v-for="item in teamLeaderOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="type">
            <el-select filterable v-model.number="addTemp.type" @ class="filter-item" placeholder="Please select">
