            </el-select>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="createData()"></el-button>
        </div>
      </el-dialog>
      <el-dialog title="" :visible.sync="addVisible1" append-to-body class="handleAddBox" center>
        <el-form ref="addForm1" :model="addTemp1" label-position="left" :rules="addRules1" class="formStyle">
          <el-form-item label="()" prop="dates">
            <el-date-picker class="filter-item multipleSelectDate" v-model="addTemp1.dates" align="right" type="dates" value-format="timestamp" placeholder="" :picker-options="pickerOptions1">
            </el-date-picker>
          </el-form-item>

          <el-form-item label="" prop="templateDate">
            <el-date-picker class="filter-item" v-model="addTemp1.templateDate" align="right" type="date" value-format="timestamp" placeholder="" :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="handleCreateData()"></el-button>
        </div>
      </el-dialog>
    </div>
    <el-dialog title="" id="mydialog" :visible.sync="editVisible" append-to-body class="alarmDealBox " center>
      <el-form ref="editForm" :model="editTemp" label-position="left" :rules="editRules" class="formStyle">
        <el-form-item label="" prop="modifyDates">
          <el-select filterable multiple collapse-tags @change="modifyDatesSelectChange" v-model="editTemp.modifyDates" class="filter-item" placeholder="Please select">
            <el-option v-for="item in dateArr" :key="item.value" :label="item.text" :value="item.value" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="types">
          <el-select filterable v-model="editTemp.types" @change="typesSelectChange" filterable multiple class="filter-item" placeholder="Please select">
            <el-option v-for="item in typeOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="carId">
          <el-select filterable v-model.number="editTemp.carId" class="filter-item" placeholder="Please select" @change="selectChange(editTemp,'car')" :class="{repeatColor:editTempRepeatCar==editTemp.carId,removeClass:judgeRemoveIn(carOptions,editTemp.carId)}">
            <el-option v-for="item in carOptions" :key="item.id" :label="item.licenseNo" :value="item.id" :disabled="item.remove" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="leaderId">
          <el-select filterable v-model.number="editTemp.leaderId" class="filter-item" placeholder="Please select" @change="selectChange(editTemp,'group')" :class="{repeatColor:editTempRepeatPersons.indexOf(editTemp.leaderId)!==-1,removeClass:judgeRemoveIn(teamLeaderOptions,editTemp.leaderId)}">
            <el-option v-for="item in teamLeaderOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <div class="editItem" :class="{red:editTempRepeatPersons.indexOf(editTemp.driverId)!==-1}">{{editTemp.driverName}}</div>
        </el-form-item>
        <el-form-item label="1">
          <div class="editItem" :class="{red:editTempRepeatPersons.indexOf(editTemp.safetyId)!==-1}">{{editTemp.safetyName}}</div>
        </el-form-item>
        <el-form-item label="2">
          <div class="editItem" :class="{red:editTempRepeatPersons.indexOf(editTemp.additionSafetyId)!==-1}">{{editTemp.additionSafetyName}}</div>
        </el-form-item>
        <el-form-item label="1">
          <div class="editItem" :class="{red:editTempRepeatPersons.indexOf(editTemp.businessId)!==-1}">{{editTemp.businessName}}</div>
        </el-form-item>
        <el-form-item label="2">
          <div class="editItem" :class="{red:editTempRepeatPersons.indexOf(editTemp.addBusinessId)!==-1}">{{editTemp.addBusinessName}}</div>
        </el-form-item>

      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" size="mini" @click="editData()"></el-button>
      </div>
    </el-dialog>
    <!-- <el-pagination background layout="prev, pager, next, sizes, total, jumper" :page-sizes="[5, 10, 15, 20]" :page-size="pagesize" :total="tableForm.list.length" v-if="tableForm.list.length" :current-page="currpage" @current-change="handleCurrentChange" @size-change="handleSizeChange"></el-pagination> -->

  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import { generate, queryPlan, queryExp, publishplan, addPlanSchedule, updateplan, manualPlanScheduling } from "../../../api/schedule/schedule";
import { carGroupAlllist } from "../../../api/managementArchives/unitManagement";
import { formatDate } from "../../../../src/common"; //
import { personsSelect, carsSelect, mapSelect, exportExcelReq } from "../../../api/managementArchives/common"; //
import { addZero, renderTime } from "../../../../src/common"; //

let myLoading = undefined; // 

//
const unSelect = (rule, value, callback) => {
  if (value == -1) {
    callback(new Error(""));
  } else {
    callback();
  }
};
//
const modifyDatesVerify = (rule, value, callback) => {
  if (!value) {
    callback(new Error(""));
  } else {
    if (!value.length) {
      callback(new Error(""));
      return;
    }
    callback();
  }
};

const modifyTypesVerify = (rule, value, callback) => {
  if (!value) {
    callback(new Error(""));
  } else {
    if (!value.length) {
      callback(new Error(""));
      return;
    }
    callback();
  }
};
export default {
  name: "schedualChild",
  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 
      tableKey: 0,
      tableForm: { list: [] },
      total: 0,
      indexMethod: 1, // 
      listLoading: true,
      listQuery: {
        selectDate: new Date()
      },
      //
      temp: {
      },
      dialogStatus: "", //
      textMap: {
        update: "",
        create: ""
      },
      carOptions: [{ id: -1, licenseNo: "" }], //
      geometryOptions: [{ id: -1, name: "" }], //
      typeOptions: [
        { id: 2, name: "" },
        { id: 1, name: "" }],
      statusOptions: [
        { id: 1, name: "" },
        { id: 2, name: "" }
      ],
      statusOptions1: [
        { id: 3, name: "" },
        { id: 4, name: "" }
      ],
      statusOptions2: [
        { id: 5, name: "" },
        { id: 6, name: "" }
      ],
      teamLeaderOptions: [{ id: -1, name: "" }], //
      //
      rules: {
        teamName: [
          { required: true, message: "", trigger: "blur" }
        ],
        status: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      //
      listRules: {
        routeId: [
          { required: true, message: "", trigger: "change", validator: unSelect }
        ],
        type: [
          { required: true, message: "", trigger: "change", validator: unSelect }
        ],
        carId: [
          { required: true, message: "", trigger: "change", validator: unSelect }
        ],
        leaderId: [
          { required: true, message: "", trigger: "change", validator: unSelect }
        ],
        driverName: [
          { required: true, message: "", trigger: "blur" }
        ],
        businessName: [
          { required: true, message: "", trigger: "blur" }
        ],
        safetyName: [
          { required: true, message: "", trigger: "blur" }
        ],



      },
      //
      modifyRules: {
        modifyReason: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      },

      addRules: {
        addDate: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        routeId: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        carId: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        leaderId: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        type: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        status: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      },
      editVisible: false,
      editTemp: {
        modifyDates: [],
        types: [],
      },
      editRules: {


        carId: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        leaderId: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        modifyDates: [
          { required: true, message: "", trigger: "change", validator: modifyDatesVerify }
        ],
        types: [
          { required: true, message: "", trigger: "change", validator: modifyTypesVerify }
        ]
        // safetyId: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "change"
        //   }
        // ],
        // additionSafetyId: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "change"
        //   }
        // ],
        // businessId: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "change"
        //   }
        // ],
        // addBusinessId: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "change"
        //   }
        // ],
        // driverId: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "change"
        //   }
        // ],
      },
      pickerOptions: {
        shortcuts: [{
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }],
        disabledDate: (time) => {
          return time.getTime() > new Date().getTime()
        },
      },
      pickerOptions1: {
        // shortcuts: [{
        //   text: '',
        //   onClick(picker) {
        //     const end = new Date();
        //     const start = new Date();
        //     start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
        //     picker.$emit('pick', [start, end]);
        //   }
        // }, {
        //   text: '',
        //   onClick(picker) {
        //     const end = new Date();
        //     const start = new Date();
        //     start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
        //     picker.$emit('pick', [start, end]);
        //   }
        // }, {
        //   text: '',
        //   onClick(picker) {
        //     const end = new Date();
        //     const start = new Date();
        //     start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
        //     picker.$emit('pick', [start, end]);
        //   }
        // }],
        disabledDate: (time) => {
          return time.getTime() < this.startTimeTimestamps || time.getTime() > this.startTimeTimestamps + 6 * 3600 * 24 * 1000
        },
        // disabledDate: (time) => {
        //   return time.getTime() < (new Date(this.getFirstDayOfWeek(new Date(new Date().getTime())).replace(/_/g, '/')).getTime())
        // },
      },
      modifyTemp: { modifyReason: '' },
      modifyVisible: false,
      modifyData: {},
      timer: null,
      addVisible: false,
      addTemp: { addDate: this.formatDateTime(new Date()) },
      showGenerate: false,//
      showPublish: false,
      dateArr: [],//
      filteredValue: [],//
      typeArr: [{ text: '', value: 2 }, { text: '', value: 1 }],
      filteredTypeValue: [],
      fullscreenLoading: null,
      addVisible1: false,
      addTemp1: {},
      addRules1: {},
      getSelectPromises: [],
      dateDisabled: false,//
      hasNormalSchedule: false,
      addRules1: {
        dates: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ], templateDate: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
      },
      pagesize: 5,
      currpage: 1,
      editTempRepeatPersons: [],//
      editTempRepeatCar: null,


    };
  },
  computed: {
    startTimeTimestamps: function () {
      return new Date(this.getFirstDayOfWeek(this.listQuery.selectDate).replace(/_/g, '/')).getTime()
    }
  },
  created() {
    this.getList(); //
    this.getSelect();
    // sessionStorage.removeItem('schedule')
    // sessionStorage.removeItem('schedulingId')
    // sessionStorage.removeItem('time')
    // sessionStorage.removeItem('schedulingDate');

  },
  mounted() {
    this.checkDate()
  },
  methods: {
    //
    typesSelectChange(val) {
      if (val.indexOf(this.editTemp.type) == -1) {
        val.push(this.editTemp.type);
      }
      this.editFilter();
      this.editCarFilter()

    },
    modifyDatesSelectChange(val) {
      if (val.indexOf(this.editTemp.sortDate) == -1) {
        val.push(this.editTemp.sortDate);
      }
      this.editFilter();
    },
    handleFilterChange(filters) {
      this.filteredValue = filters.sortDate
      this.filteredTypeValue = filters.type
    },
    handleCurrentChange(cpage) {
      this.currpage = cpage;
      $(".el-tabs__content").scrollTop(0);
    },
    handleSizeChange(psize) {
      this.pagesize = psize;
    },
    formateType(type) {
      let text = ''
      switch (type) {
        case 1:
          text = ''
          break;
        case 2:
          text = ''
          break;

        case 5:
          text = ''
          break;

        case 7:
          text = ''
          break;


      }
      return text;

    },
    formateStatus(status) {
      let text = ''
      switch (status) {
        case 1:
          text = ''
          break;
        case 2:
          text = ''
          break;

        case 3:
          text = ''
          break;

        case 4:
          text = ''
          break;


      }
      return text;

    },
    leaderChange(row) {
      let groupMsg = this.findItem(this.groupAlllist, 'leaderId', row.leaderId);
      row.leaderName = groupMsg.leaderName;
      row.safetyId = groupMsg.safetyId;
      row.safetyName = groupMsg.safetyName;
      row.additionSafetyId = groupMsg.additionSafetyId;
      row.additionSafetyName = groupMsg.additionSafetyName;
      row.businessId = groupMsg.businessId;
      row.businessName = groupMsg.businessName;
      row.addBusinessId = groupMsg.addBusinessId;
      row.addBusinessName = groupMsg.addBusinessName;
      row.groupId = groupMsg.id;
      row.groupName = groupMsg.groupName;
      row.driverId = groupMsg.driverId;
      row.driverName = groupMsg.driverName;
      this.editFilter()
    },
    //
    isSameWeek(old, now) {
      let oneDayTime = 1000 * 60 * 60 * 24;
      let old_count = parseInt(old.getTime() / oneDayTime);
      let now_other = parseInt(now.getTime() / oneDayTime);
      if (parseInt((old_count + 4) / 7) < parseInt((now_other + 4) / 7)) {

        return true;
      }

    },
    checkDate() {
      let now = new Date();
      let old = this.listQuery.selectDate;
      if (this.isSameWeek(old, now)) {
        this.dateDisabled = true;
      } else {
        this.dateDisabled = false;

      }
    },
    changegToGetList(val) {
      this.checkDate()
      this.getList();
    },
    filterTag(value, row) {
      return row.sortDate === value;
    },
    filterTypeTag(value, row) {
      return row.type === value;

    },
    formatTime(time) {
      return renderTime(time);
    },
