      // });
    },
    //
    generateSchdule() {
      generate({
        startTime: new Date(this.getFirstDayOfWeek(this.listQuery.selectDate).replace(/_/g, '/')).getTime(),
        days: 7,
        range: 7
      })
        .then(res => {
          if (res.status == 0) {

            this.queryGenerate(res.data);
          } else {
            this.$message.error(res.msg);
          }
        }).catch(err => {
          this.fullscreenLoading.close()
        })
    },
    publishSchdule() {
      publishplan({
        dates: this.formatDates(this.listQuery.selectDate)
      }).then(res => {
        if (res.status == 0) {

          this.queryGenerate(res.data);
        } else {
          this.$message.error(res.msg);
        }
      }).catch(err => {
        this.fullscreenLoading.close()
      })
    },
    queryGenerate(resMsg) {
      let that = this;
      queryExp({
        uuid: resMsg
      })
        .then(res => {
          if (res.status == 0) {
            //
            let msg;
            if (res.data.success == 0) {
              if (this.timer) {
                clearInterval(this.timer);
              }
              this.timer = setInterval(function () {
                that.queryGenerate(resMsg);
              }, 10000)
            } else if (res.data.success == 1) {
              msg = res.data.msg;
              this.getList(true);
              if (this.timer) {
                clearInterval(this.timer);
              }
              this.$message.success('');
              this.fullscreenLoading.close()

            } else if (res.data.success == 2) {
              msg = res.data.msg;
              if (this.timer) {
                clearInterval(this.timer);
              }
              this.$message.error('');
              this.fullscreenLoading.close()

            }

          } else {
            this.$message.error(res.msg);
          }
        }).catch(err => {
          if (this.timer) {
            clearInterval(this.timer);
          }
          this.fullscreenLoading.close()
        })

    },

    //
    formatDateTime(date) {
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      // m = m < 10 ? "0" + m : m;
      var d = date.getDate();
      // d = d < 10 ? "0" + d : d;
      var h = date.getHours();
      var minute = date.getMinutes();
      minute = minute < 10 ? "0" + minute : minute;
      return y + "_" + m + "_" + d;
    },
    gotask(row) {
      sessionStorage.schedule = 'schedualChild';
      sessionStorage.schedulingId = row.idIdentify;
      sessionStorage.schedulingDate = row.planYear + '_' + row.planMonth + '_' + row.planDay
      sessionStorage.time = this.listQuery.selectDate;
      sessionStorage.schedulePublish = row.publish;
      this.$router.push('/schedualManagement/schedualChild/task');

