            if (item.idIdentify == this.editTemp.idIdentify) {
              flag = true;
            } else if ((item.type !== this.editTemp.type || item.sortDate !== this.editTemp.sortDate) && commonTrueOrFalse) {
              flag = true;
            }

            return flag;
          })
          // //
          // addData = this.tableForm.list.filter((item, index) => {
          //   return (subData.routeId == item.routeId) && (this.editTemp.types.indexOf(item.type) >= 0) && (subData.status == item.status) && (this.editTemp.modifyDates.indexOf(item.sortDate) >= 0)
